"use client";

import React, { useState } from "react";
import Input from "@/components/Input";

interface ContactFormProps {
  endpoint: string; 
  successMessage?: string;
  subject?: string; 
  template?: string;
}

const ContactForm: React.FC<ContactFormProps> = ({
  endpoint,
  successMessage = "Message sent successfully!",
  subject = "New Contact Form Submission",
  template = "table",
}) => {
  const [email, setEmail] = useState<string>("");
  const [name, setName] = useState<string>("");
  const [message, setMessage] = useState<string>("");
  const [error, setError] = useState<string>("");

  // Handlers for input changes
  const handleEmailChange = (e: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement>) => {
    setEmail(e.target.value);
  };

  const handleNameChange = (e: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement>) => {
    setName(e.target.value);
  };

  const handleMessageChange = (e: React.ChangeEvent<HTMLTextAreaElement | HTMLInputElement>) => {
    setMessage(e.target.value);
  };

  // Form submission handler
  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();

    try {
      const response = await fetch(endpoint, {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
          Accept: "application/json",
        },
        body: JSON.stringify({
          email,
          name,
          message,
          _subject: subject,
          _template: template,
        }),
      });

      const data = await response.json();

      if (data.success) {
        setEmail("");
        setName("");
        setMessage("");
        setError(successMessage);
      } else {
        setError("Failed to send message. Please try again.");
      }
    } catch (error) {
      console.error("Error sending email:", error);
      setError(`An error occurred while sending your message. Please try again later.`);
    }
  };

  // Check if the form is valid
  const isFormValid = email && name && message;

  return (
    <form onSubmit={handleSubmit} className="px-2 py-5 text-sm">
      <Input
        title="Email"
        titleClass="text-sm"
        type="email"
        placeholder="Your@email.com"
        value={email}
        onChange={handleEmailChange}
        className="mb-4"
      />
      <Input
        title="Name"
        titleClass="text-sm"
        type="text"
        placeholder="Your name"
        value={name}
        onChange={handleNameChange}
        className="mb-4"
      />
      <Input
        title="Message"
        titleClass="text-sm"
        type="textarea"
        rows={4}
        placeholder="Your message"
        value={message}
        onChange={handleMessageChange}
        className="mb-4"
      />
      {error && (
        <p className={`text-sm ${error.includes("successfully") ? "text-green-500" : "text-red-500"}`}>
          {error}
        </p>
      )}
      <button
        type="submit"
        disabled={!isFormValid}
        className="w-full py-2 px-4 mt-6 bg-[#7842BF] text-white rounded-lg disabled:opacity-50 disabled:cursor-not-allowed"
      >
        Send Message
      </button>
    </form>
  );
};

export default ContactForm;
