import React from 'react'

interface HeaderProps {
    title: string,
    titleClass?: string,
    title2: string,
    title2Class?: string
}

function Header({title, titleClass, title2, title2Class}: HeaderProps): React.ReactElement {
  return (
    <h5 className={`font-outfit uppercase lg:text-8xl font-bold ${titleClass || ''}`}>
        {title}<br /> 
        <span className={`text-[#7842BF] uppercase opacity-70 ${title2Class || ''}`}>{title2}</span>
    </h5>
  )
}

export default Header