import React, { ChangeEvent } from 'react';

interface InputProps {
    title: string;
    titleClass?: string;
    type?: 'text' | 'email' | 'password' | 'number' | 'textarea'; // Include 'textarea' as an option
    placeholder: string;
    rows?: number; // Optional, relevant for textarea only
    value: string;
    className?: string;
    onChange: (e: ChangeEvent<HTMLTextAreaElement | HTMLInputElement>) => void; // Accepts both types
}

const Input = ({ 
    title, 
    titleClass, 
    type = 'text', 
    placeholder, 
    rows, 
    value, 
    className, 
    onChange 
}: InputProps) => {
    const isTextarea = type === 'textarea'; // Check if it should render a textarea

    return (
        <div className="w-full">
            <label className={`text-lg font-bold ${titleClass}`}>{title}</label>
            {isTextarea ? (
                <textarea
                    placeholder={placeholder}
                    rows={rows} // Applicable for textarea
                    value={value}
                    onChange={onChange} // Correctly typed
                    className={`w-full px-2 py-2 border bg-zinc-900 border-zinc-700 rounded-lg focus:outline-none mt-1 focus:border-[#7842BF] ${className}`}
                />
            ) : (
                <input
                    type={type}
                    placeholder={placeholder}
                    value={value}
                    onChange={onChange} // Correctly typed
                    className={`w-full px-2 py-2 border bg-zinc-900 border-zinc-700 rounded-lg focus:outline-none mt-1 focus:border-[#7842BF] ${className}`}
                />
            )}
        </div>
    );
};

export default Input;
